* { margin: 0; padding: 0; box-sizing: border-box; }
#root { display: flex; align-items: center; flex-direction: column; }

.App { width: 880px; font-family: Arial, Helvetica, sans-serif; }
.post { display: flex; padding: 15px; border: 2px solid teal; margin-top: 15px; justify-content: space-between; align-items: center; }

.post__btns { display: flex; }
.post-enter { transform: translateX(-100vw); }
.post-enter-active { transform: translateX(0); transition: transform 500ms ease-in; }
.post-exit { transform: translateX(0); }
.post-exit-active { transform: translateX(-100vw); transition: transform 500ms ease-in; }

.page__wrapper { margin-top: 30px; display: flex; }
.page { padding: 10px; border: 1px solid teal; cursor: pointer; }
.page__current { border: 2px solid orange; font-weight: bold; }

.navbar { height: 50px; width: 100vw; display: flex; align-items: center; padding: 0 15px; background: lightgrey; }
.navbar__links { margin-left: auto; }